.toggleTheme {
    position: fixed;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    background-color: #fff;
    border: 2px solid #000;
    padding: 10px 20px;
    cursor: pointer;
}

.toggleTheme:hover {
    background-color: #000;
    color: #fff;
}

